<template>
    <v-card class="dnd-grid-container ma-2" :color="model.color" :style="model.style">
      <v-card-actions v-if="model.id">
        <v-icon class="handle">mdi-drag</v-icon>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>mdi-folder-edit</v-icon>
        </v-btn>
        <v-btn icon>
          <v-icon>mdi-folder-plus</v-icon>
        </v-btn>
      </v-card-actions>
      <v-card-title v-text="model.title"></v-card-title>
      <v-card-text>
        <dnd-container class="row" style="min-height:50px;border:1px dashed black" :dnd-id="model.id" :dnd-model="model.children">
          <template v-for="child in model.children">
            <dnd-item :key="child.id" :dnd-id="child.id" :dnd-model="child">
              <recursive-grid :model="child"/>
            </dnd-item>
          </template>
        </dnd-container>
      </v-card-text>
    </v-card>
</template>

